<template>
<div id="update-product">
<van-nav-bar
  :title="productInfo.title"
  left-arrow
  class="update-product-nav"
   @click-left="leftClick"
/>
	<div class="update-product-content">
		<van-cell-group inset >
			<h4>基础信息</h4>
			<!-- 输入任意文本 -->
			<van-field v-model="productInfo.title" label="产品名称" required maxlength="20" placeholder="请输入客户名称" />
			<van-field v-model="productInfo.specs" label="规格" required maxlength="10"  placeholder="请输入客户名称" />
	
			<van-field v-model="productInfo.color" label="颜色"  maxlength="60" placeholder="请输入网址" />
			<van-field v-model="productInfo.stock" label="库存"  maxlength="60" placeholder="请输入网址" />
			<van-field v-model="productInfo.weight" label="重量(G)"  maxlength="60" placeholder="请输入网址" />
			<van-field v-model="productInfo.sales" label="出售数量"  maxlength="60" placeholder="请输入网址" />
				<van-field name="radio" label="类型">
				<template #input>
					<van-radio-group  v-model="productInfo.typeid" direction="horizontal">
					<van-radio name="0">原材料<span style="margin-right:20px;"></span></van-radio>
					<van-radio name="1">成品</van-radio>
					</van-radio-group>
					</template>
				</van-field>

			</van-cell-group>
			<van-cell-group inset >
				<h4>联系信息</h4>
				<!-- 输入任意文本 -->
				<van-field v-model="productInfo.manufacturer" label="生产商" required maxlength="30"  placeholder="例如：123@qq.com" />
				<van-field v-model="productInfo.price" label="最低价(元)" required maxlength="60"  placeholder="请输入客户名称" />
				<van-field v-model="productInfo.costs" label="成本价(元)"  maxlength="12"  placeholder="请输入固话号码" />
				<van-field v-model="productInfo.newprice" label="最高价(元)" required maxlength="11"  placeholder="请输入手机号码" />
				<van-field v-model="productInfo.encoded" label="产品编码" maxlength="8"  required placeholder="例如：526060" />
			</van-cell-group>
			<van-cell-group inset>
				<h4>备注</h4>
				<van-field
					v-model="productInfo.content"
					rows="2"
					autosize
					type="textarea"
					maxlength="50"
					placeholder="别输入了！ 这里TM输入了也不保存...（ps：只是让TM页面看起来饱满一点）"
					show-word-limit
				/>
			</van-cell-group>
	</div>
	<div class="bottom-box">
		<i class="send-btn" @click="sendClick">保存</i>
	</div>
</div>
	
</template>

<script>
import { NavBar,Field, CellGroup,RadioGroup, Radio} from 'vant'

import {updateProduct,getProductDetail} from 'network/product'

export default {
  name: 'UpdateProduct',
	components:{
		[NavBar.name]:NavBar,
		[Field.name]:Field,
		[CellGroup.name]:CellGroup,
		[RadioGroup.name]:RadioGroup,
		[Radio.name]:Radio,
	},
	data(){
		return{
			productInfo:'',
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},

		//保存按钮
		sendClick(){
			//保存前需要各种验证 这里可以写进一个工具类里面
			let info=this.productInfo							//简便一下-省点字数
			let _self=this
			//各种验证
			if(this.$checkIsNull(info.title)){
				this.$toast('产品名称不能为空')
				return
			}
			if(this.$checkIsNull(info.specs)){
				this.$toast('产品规格不能为空')
				return
			}
			if(this.$checkIsNull(info.weight)){
				this.$toast('产品重量不能为空')
				return
			}
			if(this.$checkIsNull(info.manufacturer)){
				this.$toast('生产商不能为空')
				return
			}
			if(this.$checkIsNull(info.costs)){
				this.$toast('成本价不能为空')
				return
			}
			if(this.$checkIsNull(info.price)){
				this.$toast('销售价不能为空')
				return
			}
			
			//以上都通过了-写入
			updateProduct(this.productInfo).then(res=>{
				//如果成功就清除所有表单按钮并且跳转
				localStorage.setItem("productdetailid",this.productInfo.id)
				this.$router.push('/productdetail').catch(res=>{res})
				_self.$toast(res)
			})
			
		}
	},
	mounted(){
		let id=localStorage.getItem('updateproductid')
		getProductDetail(id).then(res=>{
			this.productInfo=res[0]
			//console.log(this.productInfo)
		})
	},
}
</script>

